#all,#study{
  margin-top: 20px;
  margin-left: 6%;
  margin-right: 6%;
}
#title{
  display: flex;
  flex-direction: row;
  height: 50px;
}
#title-text{
  font-size: 18px;
  color: #95D2AA;
  line-height: 55px;
  font-weight: bolder;
}
#list-item{
  width: 100%;
  height: 120px;
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
}
#list-item-trimage{
  width: 55%;
  height: 120px;
  padding-right: 10px;
}
#list-item-img{
  width: 100%;
  height: 120px;
}
#list-item-text{
  width: 45%;
  z-index: 9999;
  text-align: center;
  display: flex;
  flex-direction: column;
  margin-top: 8px;
}
#list-item-text-title{
  font-size: 22px;
  font-weight: 600;
  color: #4d4d4d;
}
#list-item-text-context{
  font-size: 16px;
  color: #4d4d4d;
}