*{
  padding: 0px;
  margin: 0px;
  background-color: #f5f5f5;
}
#nav{
  width: 90%;
  height: 100px;
  line-height: 100px;
  margin-left: 5%;
  margin-right: 5%;
  display: flex;
  flex-direction: row;
  border: 1px solid #95D2AA;
  border-radius: 20px;
  margin-top: 20px;
  overflow: hidden;
}
#nav-item{
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 60px;
  text-align: center;
  margin-top: 10px;
  padding-left: 10px;
}
#nav-icon{
  position: relative;
  flex: 1;
  font-size: 30px;
}
#nav-activity-text{
  position: relative;
  margin-top: 10px;
  color: black;
}
#all,#study{
  margin-top: 20px;
  margin-left: 6%;
  margin-right: 6%;
}
#title{
  display: flex;
  flex-direction: row;
  height: 50px;
}
#title-text{
  font-size: 18px;
  color: #95D2AA;
  line-height: 55px;
  font-weight: bolder;
}
#list-item{
  width: 100%;
  height: 120px;
  display: flex;
  flex-direction: row;
}
#list-item-trimage{
  width: 55%;
  height: 120px;
}
#list-item-angle1{
  width: 5px;
  height: 131px;
  background: linear-gradient(-70deg, #f5f5f5 50%, transparent 50% ) ;
  position: absolute;
  padding-left: 81%;
  overflow: hidden;
}
#list-item-img1{
  width: 100%;
  height: 120px;
}
#list-item-text{
  width: 45%;
  z-index: 9999;
  text-align: center;
  display: flex;
  flex-direction: column;
  margin-top: 8px;
}
#list-item-text-title{
  font-size: 22px;
  font-weight: 600;
  color: #4d4d4d;
}
#list-item-text-context{
  font-size: 16px;
  color: #4d4d4d;
}
#list-item-angle2{
  width: 5px;
  height: 132px;
  background: linear-gradient(110deg, #f5f5f5 50%, transparent 50% ) ;
  position: absolute;
  padding-left: 11%;
  overflow: hidden;
}
#list-item-img2{
  width: 100%;
  height: 120px;
}
