*{
    padding: 0px;
    margin: 0px;
}
nav{
    background-color: #f5f5f5;
    height: 60px;
    width: 100%;
    display: fixed;
    box-shadow: 0px 1px 1px #dedede;
    z-index: 999;
}
.iconfont{
    width: 10%;
    height: 30px;
    color:#95D2AA;
    margin-top: 20px;
    float: left;
}
.barname{
    width: 80%;
    float: left; 
    margin-top: 20px;
    text-align: center;
}
button{
    width: 10%;
    border: 0;
    background-color: #f5f5f5;
    float: right;
    margin-top: 20px;
}
span{
    width: 22px;
    height: 2px;
    border-radius: 1px;
    background-color: #95D2AA;
    display: block;
    margin-top: 4px;
    margin-right: 5px;
}
.menu{
    width: 60%;
    height: 92px;
    background-color: #95D2AA;
    margin-top: 45px;
    margin-left: 38%;
    border-radius: 10px 0 10px 10px;
    position: absolute;
}
ul{
    display: block;
}
li{
    width: 96%;
    height: 25px;
    margin-top: 4px;
    margin-bottom: 4px;
    margin-left: 2%;
    background-color: #f5f5f5;
    border-radius: 5px;
    list-style-type: none;
    text-align: center;
}
a{
    width: 100%;
    height: 100%;
    text-decoration: none;
    line-height: 25px; 
}